<template>
    <el-container>
        <el-header>
            <h3>这儿可以清除系统缓存</h3>
        </el-header>
        <el-main>
            <el-row>
                <el-col>
                    <el-table
                            :data="tableData"
                            border
                            stripe
                            style="width: 100%;">

                        <el-table-column
                                prop="desc"
                                label="描述">
                        </el-table-column>


                        <el-table-column
                                label="操作"
                                width="185">
                            <template slot-scope="scope">
                                <el-button @click="deleteHandleClick(scope.row.type)" type="danger" size="mini"
                                           icon="el-icon-delete">
                                    清除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>

        </el-main>
    </el-container>
</template>

<script>
    export default {
        data() {
            return {
                tableData: [
                   {desc: 'Banner缓存', type: 'banner'},
                    /*  {desc: '商品缓存', type: 'product'},
                     {desc: '商品分类缓存', type: 'classify'},*/
                    {desc: '菜单缓存', type: 'menu'}
                ],
            }
        },
        methods: {
            deleteHandleClick(type) {
                this.axios.post('/api/cache/clear/' + type).then((response) => {
                    if (response.data.state == '0') {
                        //刷新数据
                        this.$message({
                            type: 'success',
                            message: response.data.message
                        });
                    } else {
                        this.$message({
                            type: 'error',
                            message: response.data.message
                        });
                    }
                }).catch((response) => {
                    console.log(response);
                })
            }
        }
    }
</script>

<style scoped>

</style>
